.header{width:100vw;min-height:80vh;background-color:#2d2e32;display:grid;grid-template-columns:repeat(2, 50vw);grid-template-areas:'text profile  ';background-image:url(/static/media/egypt1.e0bbc256.png);background-repeat:no-repeat;background-size:contain;background-position:center}.header .text{padding:2rem;grid-area:text;display:flex;flex-direction:column;align-items:flex-start;justify-content:space-around;margin:5rem 0 7rem 5rem}.header .text h3{color:#000;font-size:1.2rem;background-color:#64f4ab;display:inline-block;padding:0.5rem 1rem;border-radius:0.3rem;box-shadow:0 10px 10px rgba(0,0,0,0.3);margin-right:7rem;transition:all 0.3s ease-in-out}.header .text h3:hover{box-shadow:none}.header .text h1{color:#fff;font-size:4rem}.header .text p{color:#999;font-size:1.3rem;font-weight:500;width:70%}.header .text a{color:#64f4ab;font-weight:600;background-color:#232427;box-shadow:0 10px 10px rgba(0,0,0,0.1);padding:0.5rem;font-size:1.1rem;border-radius:0.5rem;transition:all 0.3s ease-in-out;cursor:pointer}.header .text a:hover{box-shadow:none;text-decoration:none}.profile{display:flex;justify-content:center;align-items:center;padding:2rem}.profile .image{background-color:#25262a;padding:1rem;border-radius:50%;margin-bottom:5rem;box-shadow:0 15px 15px rgba(0,0,0,0.2);transition:all 0.3s ease-in-out}.profile .image img{border-radius:50%;width:400px}.profile .image:hover{box-shadow:none}.animate1{-webkit-animation:fadeIn1 3s forwards;animation:fadeIn1 3s forwards}@-webkit-keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}.animate2{-webkit-animation:fadeIn2 3s forwards;animation:fadeIn2 3s forwards}@-webkit-keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}.animate3{-webkit-animation:fadeIn3 3s forwards;animation:fadeIn3 3s forwards}@-webkit-keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}.animate4{-webkit-animation:fadeIn4 3s forwards;animation:fadeIn4 3s forwards}@-webkit-keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@media (max-width: 1150px){.header{grid-template-columns:100vw;grid-template-areas:'text' 'profile'}.header .text{padding:3rem}.header .text h3{font-size:0.9rem}.header .text h1{margin:1rem 0;font-size:3rem}.header .text p{margin:2rem 0;font-size:1.2rem;width:70%}.header .text a{font-size:1rem}.profile .image img{width:300px}}@media (max-width: 900px){.header .text{align-items:center;text-align:center;padding:3rem;margin-left:0;margin-bottom:0}.header .text h3{font-size:0.9rem;margin:auto}.header .text h1{margin:2rem auto;font-size:2rem}.header .text p{margin:1rem auto;font-size:1rem;width:90%}.header .text a{margin:auto}.profile .image img{width:200px}}

.about-block{background-color:#25262a;width:100vw;min-height:100vh;display:grid;grid-template-columns:40% 60%;grid-template-areas:'skills intro';padding:4rem 0 2rem}.about-block .skills{grid-area:skills;padding:2rem;margin:2rem}.about-block .skills h3{margin:1rem;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.about-block .skills .skill{font-size:0.9rem;text-align:center;font-weight:500;color:#cda55f;background-color:#2d2e32;display:inline-block;border-radius:0.5rem;box-shadow:0 10px 10px rgba(0,0,0,0.2);margin:1rem;padding:1rem 1rem 0.5rem;transition:all 0.3s ease-in-out}.about-block .skills .skill:hover{box-shadow:none}.about-block .skills .skill-set{margin:4rem 0;display:flex;flex-wrap:wrap;justify-content:center;align-items:center}.about-block .skills .skill-set p{margin:1rem;font-size:1.2rem;color:#999;font-family:'Luckiest Guy', cursive}.about-block .skills .skill-set .linux{font-family:'Press Start 2P', cursive;font-size:1rem;color:#fff;text-transform:uppercase}.about-block .skills .skill-set .responsive{font-size:1.2rem;color:#f0bf6c;font-family:'Monoton', cursive}.about-block .skills .skill-set .react{color:#ddd}.about-block .skills .skill-set .css{color:#fcb131}.about-block .intro{grid-area:intro;padding:2rem;margin:2rem}.about-block .intro h4{margin:2rem 0;color:#999;font-weight:600;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent;text-shadow:0 5px 5px rgba(0,0,0,0.2)}.about-block .intro h1{margin:4rem 0;color:#fff;font-weight:700}.about-block .intro h1 span{margin:0 0 4rem;display:block;font-weight:500;color:#cda55f}.about-block .intro h3{color:#fff;font-size:1.5rem;margin:1rem 0 2rem}.about-block .intro p{width:80%;color:#cda55f;font-size:1.1rem;font-weight:600;line-height:2.5rem}.about-block .intro p.center{color:#cda55f}.about-block .intro hr{background-color:#000000;width:50%}@media (max-width: 1100px){.about-blo