.product{width:400px;text-align:center;padding:1rem;border-radius:1rem;transition:all 0.3s ease-in-out;margin:1rem;position:relative}.product h2{font-size:1.3rem;color:#cda55f;text-transform:uppercase;font-family:'Luckiest Guy', cursive}.product img{width:350px;border-radius:0.5rem;margin:0.5rem;display:block;position:relative;box-shadow:0 10px 10px rgba(0,0,0,0.3)}.product .product-button{position:absolute;transform:translate(-50%, 100%);margin-right:-50%;top:50%;left:50%;background-color:rgba(0,0,0,0.8);box-shadow:0 10px 10px rgba(0,0,0,0.2);border:1px solid #fdb12c;padding:0.2rem 1rem;display:block;color:#fdb12c;border-radius:0.5rem;font-weight:bold;transition:all 0.3s ease-in-out;font-size:0.9rem}.product .product-button:hover{box-shadow:none;padding:0.2rem 1.5rem}.product .git-button{transform:translate(-50%, 250%)}.animate1{-webkit-animation:fadeIn1 3s forwards;animation:fadeIn1 3s forwards}@-webkit-keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}.animate2{-webkit-animation:fadeIn2 3s forwards;animation:fadeIn2 3s forwards}@-webkit-keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}.animate3{-webkit-animation:fadeIn3 3s forwards;animation:fadeIn3 3s forwards}@-webkit-keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}.animate4{-webkit-animation:fadeIn4 3s forwards;animation:fadeIn4 3s forwards}@-webkit-keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@media (max-width: 500px){.product{w