.works{width:100vw;background-color:#2d2e32;padding:4rem}.works h2{margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.works .project-container{display:flex;justify-content:space-around;flex-wrap:wrap}.works button{padding:0.2rem 1rem;border-radius:0.5rem;font-size:1rem;border:none;box-shadow:0 10px 10px rgba(0,0,0,0.2);display:block;margin:4rem auto 2rem;transition:all 0.3s ease-in-out;background-color:#25262a;border:1px solid #64f4ab;color:#64f4ab}.works button:hover{box-shadow:none;padding:0.2rem 1.5rem}@media (max-width: 500px){.works{padding:1rem}.works h2{font-size:1.5rem}.works .project-container .project{width:100%;padding:0.5rem}.works .project-container .project img{width:100%}.works .project-container .project h3{font-size:1.2rem}.works .project-container .project .tools div{font-size:0.9rem}}

