.contact{background-color:#25262a;width:100vw;display:grid;grid-template-columns:repeat(2, 50vw);grid-template-areas:'info social';border-top:1px solid #444;padding:4rem 0 1rem}.contact .info{text-align:center;grid-area:info;padding:1rem}.contact .info h3{font-size:1.5rem;margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.contact .info .email{color:#fff;font-weight:600}.contact .info .email a{color:#cda55f}.contact .info .footer{color:#fff}.contact .social{grid-area:social;text-align:center}.contact .social h3{font-size:1.3rem;margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.contact .social .social-icons{background-color:#cda55f;display:inline-block;padding:0.3rem;border-radius:0.5rem;box-shadow:0 10px 10px rgba(0,0,0,0.2)}.contact .social .social-icons img{margin:0 1rem;width:30px}.contact .social .phone{color:#cda55f;margin:1rem 0;font-weight:bold}.contact .social .phone i{font-size:1.3rem}.contact .social .phone a{margin:0 0.5rem;color:#cda55f}@media (max-width: 600px){.contact{padding:1rem;grid-template-columns:100%;grid-template-areas:'info' 'social'}.contact .info{margin:1rem 0}.contact .social{margin:1rem 0}.contact .social .social-icons{padding:0.5rem}.contact .social .social-icons img{width:20px;margin:0 0.5rem}}

